@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@400;500;700&display=swap');

html,body{
    margin: 0;
    font-family: 'Space Grotesk', sans-serif;
    color:#fff;
}

h1{
    font-size: 40px;
}
h2{
    font-size:40px;
    font-weight: 700;
  }
  h3{
    font-size: 24px;
    font-weight: 700;
    margin: 0;
  }
p{
    font-size: 20px;
    font-weight: 700;
    margin:0;
}

.flex-column
{
    display: flex;
    flex-direction: column;
    &.mobile-row{
        flex-direction: row;
    }
    &.center-align{
        align-items: center;
    }
    &.left-text{
        text-align: left;
    }
}

.flex-row{
    display: flex;
    flex-direction: row;
    &.mobile-column{
        flex-direction: column;
    }
}

.header{
    margin: 5% auto auto;
    width: 95vw;
    background-color: #00000080;
    filter: blur(60%);
    display:grid;
    flex-direction: row;
    grid-template-columns: repeat(3,1fr);
    border-radius: 5px;
    align-items: center;
    .mobile-menu{
        display: none;
    }
    @media screen and (max-width: 430px){
    min-height:10vh;
    grid-template-columns: repeat(2,1fr);
    .logo-section{
        img{
            width: 67px;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-left: 20px;
        }
    }
        .cta-section{
            display: none;
        }
        .nav-section{
            display:none;
        }
        .mobile-menu{
            display: flex;
            justify-content: flex-end;
            font-size: 30px;
            margin-right: 20px ;
        }
    }
}

.hero-header {
    display:flex;
    flex-direction: column;
    width: 100vw;
    height: 100vh;
    color:#fff;
    &.home {
      background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), 
      url("/images/home/hero-header.webp");
      background-position: center right;
      background-size: cover;
      @media screen and (max-width: 430px){
        background-position: 60% center;
    }
    }

  }

  .hero-text{
    &.home{
    margin:1% 20px auto;
    }
}

.cta-button{
    &.home{
        margin:10% auto 0;
        display: flex;
        background: #fff;
        color:#000;
        text-align: center;
        border-radius: 5px;
        width: 85vw;
        min-height: 58px ;
        justify-content: center;
        align-items: center;
        font-weight: 700;
        font-size: 20px;
    }
}
.secondary-cta-button{
    &.home{
        margin:10% auto 0;
        display: flex;
        border: 2px solid #fff;
        color:#fff;
        text-align: center;
        border-radius: 5px;
        width: 85vw;
        min-height: 58px ;
        justify-content: center;
        align-items: center;
        font-weight: 700;
        font-size: 20px;
    }
}

.blank-section{
    @media screen and (max-width: 430px){
        display:none;
    } 
}

.content-area
{
    &.home-2{
            background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), 
            url("/images/home/home-bg-mobile.webp");
            background-position: center;
            background-size: cover;
            @media screen and (max-width: 430px){
              background-position: 60% center;
              padding: 5%;
          } 
    }
    .item-list{
        @media screen and (max-width: 430px){
            margin: 10% 0;
            padding: 0px 5%;
            p{
                margin: 5% 0 0 0;
            }
        } 
    }
}

.flourish{
    @media screen and (max-width: 430px){
        font-size: 24px;
        font-weight: 700;
    } 
}

.content-icon{
    @media screen and (max-width: 430px){
        width: 48px;
        margin: 10% 0;
    } 
}